.NotFound {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}
.NotFoundText {
  font-family: 'Inter';
  line-height: 24px;
  width: 100vw;
  padding: 20px;
  text-align: center;
}
.NotFoundButton {
  background-color: #0284c7;
  color: white;
  font-size: 20px;
  font-weight: 600;
  border: none;
  padding: 10px;
  border-radius: 10px;
  cursor: pointer;
}
